
import { AjaxHttp, AjaxSilentHttp } from '../../../js/ajaxHttp'
import { getYMDFormateTime, getHMSFormateTime, isArrayType, getTimestamp, getHMFormateTime, isNullValue, getYMDFormateTimestamp } from "../../../js/utility.js"
import ListCell from '../../crm/components/ListCell.vue'
import { mapState } from 'vuex'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'

import { PLATFORM } from '../../../config.js'
import PlateType from '../../../js/enum.js'

export default {
name: 'officework',
components: {
ListCell,
Datetime,
Tabbar,
TabbarItem,
Popup,
XTextarea
},
computed: {
...mapState([
'userInfo',
'detail',
]),
currentTime() {
return getHMFormateTime(getTimestamp(new Date()));
},
endDate() {
return getYMDFormateTime(getTimestamp(new Date()));
}
},
data() {
return {
selectDateF: '',
date: '',
dateFormate: '',
address: '',
positionName: '',
daka_status: '', //1 签到 2签退
shangban_time: '',
shangban_info: {}, //上班信息
shangban_status: '', // 展示类型
xiaban_time: '',
xiaban_info: {}, //下班信息
xiaban_status: '', // 展示类型
datalist: [],
timerIDs: [],
config_info: {}, //考勤信息
hasConfigWifi: false, //有wifi配置
hasConfigLocation: false, //有签到位置配置
accessWifi: false, //接入wifi
accessWifiName: '', //接入的wifi名称
accessWifiMac: '', //接入的wifi地址
accessLocation: false, //接入位置
accessStatusInfo: '', //顶部展示的状态
verifySignAccess: false, // 可以内勤签到
signButtomTitle: '',
timer: null, //定时器

datePickerVisibility: false,
addPopupShow: false,
popupContent: '',
setting: 0, //时候有设置权限

}
},
beforeRouteLeave(to, from, next) {
for (var i = 0; i < this.timerIDs.length; i++) {
clearTimeout(this.timerIDs[i]);
}
this.timerIDs = [];
clearInterval(this.timer);
this.timer = null;

if (to.name === 'office') {
this.$store.commit('updateDetail', '');
this.$store.commit('updateCheckStatisInfo', {}); 
this.$store.commit('updateCheckSetInfo', {});
} else {
this.$store.commit('updateDetail', {
type: 'officework',
address: this.address,
selectDateF: this.selectDateF,
positionName: this.positionName,
daka_status: this.daka_status,
shangban_time: this.shangban_time,
shangban_info: this.shangban_info,
shangban_status: this.shangban_status,
xiaban_time: this.xiaban_time,
xiaban_info: this.xiaban_info,
xiaban_status: this.xiaban_status,
datalist: this.datalist,

config_info: this.config_info,
hasConfigWifi: this.hasConfigWifi,
hasConfigLocation: this.hasConfigLocation,
accessWifi: this.accessWifi,
accessWifiName: this.accessWifiName,
accessWifiMac: this.accessWifiMac,
accessLocation: this.accessLocation,
accessStatusInfo: this.accessStatusInfo,
verifySignAccess: this.verifySignAccess,
signButtomTitle: this.signButtomTitle,

setting: this.setting,

});

}

next();
},
mounted() {
if (this.detail !== '' && this.detail.type === 'officework'&& !this.detail.refresh) {
this.address = this.detail.address;
this.$nextTick(() => {
this.selectDateF = this.detail.selectDateF;
});
this.positionName = this.getPositionName();
this.daka_status = this.detail.daka_status;
this.shangban_time = this.detail.shangban_time;
this.shangban_info = this.detail.shangban_info;
this.shangban_status = this.detail.shangban_status;
this.xiaban_time = this.detail.xiaban_time;
this.xiaban_info = this.detail.xiaban_info;
this.xiaban_status = this.detail.xiaban_status;
this.datalist = this.detail.datalist;

this.config_info = this.detail.config_info;
this.hasConfigWifi = this.detail.hasConfigWifi;
this.hasConfigLocation = this.detail.hasConfigLocation;
this.accessWifi = this.detail.accessWifi;
this.accessWifiName = this.detail.accessWifiName;
this.accessWifiMac = this.detail.accessWifiMac;
this.accessLocation = this.detail.accessLocation;
this.accessStatusInfo = this.detail.accessStatusInfo;
this.verifySignAccess = this.detail.verifySignAccess;
this.signButtomTitle = this.detail.signButtomTitle;

this.setting = this.detail.setting;

} else {
this.$nextTick(() => {
this.selectDateF = getYMDFormateTime(getTimestamp(new Date()));
});
this.getdetail(getYMDFormateTime(getTimestamp(new Date())));
this.getConfig(); 
}

this.intervalHandle();
this.$nextTick(() => {
this.intervaltime(function() {
if (this.daka_status !== 0 && this.daka_status !== '') {
this.intervalHandle();
}
}.bind(this), 1000);
});







document.addEventListener("resume", function onResume() {
this.getConfig();
}, false);
},
methods: {
isAndroid() {
var u = navigator.userAgent;
return u.indexOf('Android') > -1 || u.indexOf('Adr') > -1;
},
isIOS() {
var u = navigator.userAgent;
return !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);;
},
intervalHandle() {
this.date = getTimestamp(new Date());
this.dateFormate = getHMSFormateTime(this.date);
this.verificationLocaitonInfo(); 
this.verificationWifiInfo(); 
this.getAccessStatusInfo(); 
},
getdetail(time) {
this.daka_status = ''; 
AjaxHttp('POST', 'm=kaoqin&a=view', { date: time }, this, function(res, wself, success) {
if (success) {
if (res.list.length > 0) {
for (var i = 0; i < res.list.length; i++) {
var item = res.list[i];
item.daka_time = getHMFormateTime(item.daka_time);
item.status_color = wself.getStatusColor(item.status);
item.status_name = wself.getStatusName(item.status);
if (item.status === '1' || item.status === '2') {
wself.shangban_info = item;
} else if (item.status === '3' || item.status === '4') {
wself.xiaban_info = item;
}
}
}

wself.setting = res.setting;
wself.datalist = res.list;
wself.daka_status = res.daka_status; 
wself.shangban_time = res.shangban_time;
wself.xiaban_time = res.xiaban_time;
wself.shangban_status = wself.getShangban_status();
wself.xiaban_status = wself.getXiaban_status();
}
});
},

getConfig() {
AjaxSilentHttp('POST', 'm=kaoqin&a=config', {}, this, function(res, wself, success) {
if (success) {
wself.config_info = res;

if (res.config_info.x && res.config_info.x !== '') {

wself.hasConfigLocation = true;
} else {
wself.accessLocation = false;
}

if (res.route_list && res.route_list.length > 0) {

for (var i = 0; i < res.route_list.length; i++) {
var item = res.route_list[i];
item.mac_address = wself.handleMACAddress(item.mac_address);

}
wself.hasConfigWifi = true;
} else {
wself.accessWifi = false;
}

}
});
},
handleMACAddress(address) {
var items = address.split(":");
items.splice(items.length - 1, 1);
return items.join(":");
},
verificationWifiInfo() { //wifi验证放到定时器里
var self = this;


if (this.hasConfigWifi && !this.accessLocation&& PLATFORM !== 'web') { //需要验证wifi
WifiWizard.getCurrentBSSID(function ssidHandler(BSSID) {
var accessWifi = false;
for (var i = 0; i < self.config_info.route_list.length; i++) {
var item = self.config_info.route_list[i];

var judgeBSSID = self.handleMACAddress(BSSID);
if (item.mac_address === judgeBSSID) {

accessWifi = true;
self.accessWifiMac = BSSID;
WifiWizard.getCurrentSSID(function ssidHandler(SSID) {
self.accessWifiName = SSID;
}, function fail(e) {

});
}
}

self.accessWifi = accessWifi;
}, function fail(e) {
self.accessWifi = false;
self.accessWifiMac = '';
self.accessWifiName = '';

});
}
},
verificationLocaitonInfo() { //获取完配置就开始依据定位刷新
var self = this;
if (this.hasConfigLocation) { //需要验证位置
var circle = new BMap.Circle(new BMap.Point(parseFloat(this.config_info.config_info.y), parseFloat(this.config_info.config_info.x)), parseFloat(this.config_info.config_info.radius));
if (PLATFORM !== 'web' && this.isAndroid() && !this.accessWifi) {
function successCallback(data) {
if (!self.hasConfigWifi || !self.accessWifi) { //如果验证wifi并且没有正常接入的情况下 解析位置
var point = new BMap.Point(parseFloat(data.longitude), parseFloat(data.latitude));
if (BMapLib.GeoUtils.isPointInCircle(point, circle)) {
self.getCity(point);
self.accessLocation = true;
} else {
self.accessLocation = false;
}
}
};

function failedCallback(data) {
self.accessLocation = false;
self.$vux.loading.hide();

self.$vux.toast.text('定位失败', 'bottom');
};
baidu_location.getCurrentPosition(successCallback, failedCallback);

} else if (PLATFORM !== 'web' && this.isIOS() && !this.accessWifi) {
var onSuccess = function(position) {
if (!self.hasConfigWifi || !self.accessWifi) { //如果验证wifi并且没有正常接入的情况下 解析位置

var point = new BMap.Point(parseFloat(position.coords.longitude), parseFloat(position.coords.latitude));
var convertor = new BMap.Convertor();
var pointArr = [];
pointArr.push(point);
convertor.translate(pointArr, 1, 5, function(data) {

if (data.status === 0) {
var newpoint = data.points[0];
if (BMapLib.GeoUtils.isPointInCircle(newpoint, circle)) {

self.getCity(newpoint);
self.accessLocation = true;
} else {

self.accessLocation = false;
}
} else {

self.$vux.loading.hide();


}
});
}
};

function onError(error) {
self.accessLocation = false;
self.$vux.loading.hide();

self.$vux.toast.text('定位失败', 'bottom');
}
navigator.geolocation.getCurrentPosition(onSuccess, onError);

}
}
},
getAccessStatusInfo() {
if (this.accessWifi) {
this.$nextTick(() => {
this.verifySignAccess = true;
this.accessStatusInfo = '已进入Wi-Fi考勤范围：' + this.accessWifiName;
this.signButtomTitle = this.daka_status === 1 ? '上班打卡' : '下班打卡';
});

} else if (this.accessLocation) {
this.$nextTick(() => {
this.verifySignAccess = true;
this.accessStatusInfo = '已进入地理位置考勤范围：' + this.positionName;
this.signButtomTitle = this.daka_status === 1 ? '上班打卡' : '下班打卡';
});

} else {
this.$nextTick(() => {
this.verifySignAccess = false;
this.accessStatusInfo = '';
this.signButtomTitle = '外勤打卡';
});

}
},
enterUser(role_id) { //进入个人详情
this.$router.push({
name: 'user',
params: {
id: role_id
}
})
},
datePickerChange(value) {
if (value && this.selectDateF !== value) {
this.selectDateF = value;
this.getdetail(this.selectDateF);
}
},
onIndexChange(newIndex, oldIndex) {
if (!isNullValue(newIndex) && !isNullValue(oldIndex)) {
if (newIndex === 1) {
this.$router.replace({ name: 'checkstatistics' })
} else if (newIndex === 2) {
this.$router.replace({ name: 'checkset' })
}
}
},
getCity(point) {
var geoc = new BMap.Geocoder();
var self = this;
geoc.getLocation(point, function(rs) {
var addComp = rs.addressComponents;
self.$vux.loading.hide();
if (rs.surroundingPois.length > 0) {
var item = rs.surroundingPois[0];

item.wktype = 'poi';
self.address = item;
self.positionName = self.getPositionName();





} else {





rs.wktype = 'defalut';
self.address = rs;
self.positionName = self.getPositionName();
}

});
},
intervaltime(func, wait) {

var interv = function() {
func.call(null);
var time = setTimeout(interv, wait);
this.timerIDs.push(time);
}.bind(this);
var time = setTimeout(interv, wait);
this.timerIDs.push(time);

},
enterMapChoice() {
if (this.address === '') { return; }
this.$router.push({
name: 'mapchoice'
});
},
getPositionName() {
if (this.address.wktype === 'poi') {
return this.address.title;
} else if (this.address.wktype === 'defalut') {
return this.address.address;
}
return "";
},
punchClock() {
if (this.verifySignAccess) { //成功
if ((this.daka_status === 1 && getTimestamp(new Date()) > this.getHMFormateTimestamp(this.shangban_time)) || ((this.daka_status === 2 || this.daka_status === 3) && getTimestamp(new Date()) < this.getHMFormateTimestamp(this.xiaban_time))) {
this.popupContent = '';
this.addPopupShow = true;
} else {
this.updateInfo();
}
} else {
this.$router.push({
path: 'fieldsign/default'
})
}
},
popupSave() {
this.updateInfo();
this.addPopupShow = false;
},
updateInfo() {
var postParams = {};
postParams.remark = this.popupContent;
if (this.accessLocation) {
if (this.address === '') {
this.$vux.toast.text('未获取到位置信息', 'bottom');
return;
}
postParams.address = this.positionName;
postParams.x = this.address.point.lat;
postParams.y = this.address.point.lng;
postParams.config_type = 2; 
} else if (this.accessWifi) {
postParams.wifi_name = this.accessWifiName;
postParams.mac_address = this.accessWifiMac;
postParams.config_type = 1; 
}
postParams.token_id = device.uuid;
AjaxHttp('POST', 'm=kaoqin&a=add', postParams, this, function(res, wself, success) {
if (success) {
wself.getdetail(getYMDFormateTime(wself.date));
wself.$store.commit('updateCheckStatisInfo', {}); 
}
});
},
getHMFormateTimestamp(time) {
var ymd_s = getYMDFormateTime(getTimestamp(new Date()));
time = ymd_s + ' ' + time;
var t = time.split(/[- :]/);
var d = new Date(t[0], t[1] - 1, t[2], t[3], t[4], t[5]);
var timestamp = new Date(d).getTime().toString();
timestamp = timestamp.substr(0, 10);
var rightTime = parseInt(timestamp) + 59;
return rightTime.toString();
},
judgeIsNullValue(content) {
return isNullValue(content);
},
getStatusColor(status) {
if (status === '1') {
return '#198AFA'; 
} else if (status === '2') {
return '#FF8125'; 
} else if (status === '3') {
return '#FF9C00'; 
} else if (status === '4') {
return '#198AFA'; 
}
return '#198AFA';
},
getStatusName(status) {
if (status === '1') {
return '正常'; 
} else if (status === '2') {
return '迟到'; 
} else if (status === '3') {
return '早退'; 
} else if (status === '4') {
return '正常'; 
}
return '';
},
getShangban_status() {


if (this.daka_status !== '') { //'begin' 'normal' 'none'
if (this.daka_status === 0 || this.daka_status === 2 || this.daka_status === 3) { //不能打卡
var hasInfo = false; 
for (var i = 0; i < this.datalist.length; i++) {
var item = this.datalist[i];
if (item.status === '1' || item.status === '2') {
hasInfo = true;
}
}
if (hasInfo) {
return 'normal';
} else {
return 'none';
}
} else if (this.daka_status === 1) {
return 'begin';
}
}
return ''; 
},
getXiaban_status() {

if (this.daka_status !== '') { //'begin' 'normal' 'none'
if (this.daka_status === 0) { //不能打卡
var hasInfo = false; 
for (var i = 0; i < this.datalist.length; i++) {
var item = this.datalist[i];
if (item.status === '3' || item.status === '4') {
hasInfo = true;
}
}
if (hasInfo) {
return 'normal';
} else {
return 'none';
}
} else if (this.daka_status === 2) {
return 'begin';
} else if (this.daka_status === 3) { //可更新打卡
return 'normal';
}
}
return ''; 
},
getCurrentSSID() {
WifiWizard.getCurrentSSID(function ssidHandler(s) {
alert("Current SSID-" + s);
console.log("Current SSID-" + s);
}, function fail(e) {
alert("Failed" + e);
});
},
getCurrentBSSID() {
WifiWizard.getCurrentBSSID(function ssidHandler(s) {
alert("Current BSSID-" + s);
console.log("Current BSSID-" + s);
}, function fail(e) {
alert("Failed" + e);
});
},
getWifiList() {
WifiWizard.listNetworks(function listHandler(a) {
alert(JSON.stringify(a));
console.log("listNetworks" + JSON.stringify(a));
}, function fail(e) {
alert("Failed" + e);
});
},
getScanResult() {
WifiWizard.startScan(function() {
WifiWizard.getScanResults(function listHandler(a) {
alert(JSON.stringify(a));
console.log("getScanResults" + a);
}, function fail(e) {
alert("Failed" + e);
});
}, function fail(e) {
alert("Failed" + e);
});
},
getDeviceInfo() {
var info = device.platform + "---" + device.uuid + "---" + device.version + "---" + device.model + "---" + device.manufacturer + "---" + device.isVirtual + "---" + device.serial;
alert(info);
console.log(info);
}
}
}




